import React from 'react'

export default function Presentation() {
    return (
        <div id='presentation' className='presentation_container'>
            <div className='presentation_titre'>
                <h1><span className='text-gradient'>Qui suis-je ?</span></h1>
            </div>
            <div className='presentation_text'>
               <p> Bienvenue, sur mon portfolio!<br/>
               Je suis Nicolas Sckittekatte, j'ai 23 ans et je suis un dveloppeur web junior.
               Sur ce portfolio vous trouverez mes projets, mon parcours ainsi que les langages que j'ai appris durant mes formations.  
               Ayant acquis des comptences en dveloppement web, notamment en HTML, CSS, JavaScript, React, j'ai travaill sur plusieurs projets qui 
               m'ont permis de renforcer mes connaissances et d'amliorer mon exprience en dveloppement.
               Si vous tes  la recherche d'un dveloppeur web junior pour une alternance, je suis disponible et 
               motiv pour travailler avec vous.</p>
              <p> N'hsitez pas  me contacter si vous voulez en savoir plus sur mes comptences et mon exprience.</p>
            </div>
        </div>
    )
}
